import { ConnectionService } from './../../../../../shared/services/connection.service';
import { Corporation } from './../../../../../shared/models/corporaction';
import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-corporations-list',
    templateUrl: './corporations-list.component.html',
    styleUrls: ['./corporations-list.component.scss']
})
export class CorporationsListComponent implements OnInit {
    loading: boolean = false;
    corporations: Corporation[] = [];

    constructor(
        private connection: ConnectionService,
        private spinner: NgxSpinnerService,
    ) { }

    ngOnInit(): void {
        this.getCorporations();
    }

    private getCorporations(): void {
        this.loading = true;
        this.spinner.show();
        setTimeout(() => {
            this.connection.get(ConnectionService.CORPORATION).subscribe(

                (response: any) => {
                    this.loading = false;
                    this.spinner.hide();
                    this.corporations = response;

                },
                (error: any) => {
                    this.loading = false;
                    this.spinner.hide();
                    console.log(error);
                }
            );
        }, 1000);
    }

}
