import { Component, HostListener, OnInit } from '@angular/core';

@Component({
    selector: 'app-corporations-form',
    templateUrl: './corporations-form.component.html',
    styleUrls: ['./corporations-form.component.scss']
})
export class CorporationsFormComponent implements OnInit {
    public screenWidth: any;
    public screenHeight: any;


    constructor() { }

    ngOnInit(): void {
        this.screenWidth = window.innerWidth;
        this.screenHeight = window.innerHeight - 156;
        // console.log(this.screenHeight + '  ' + this.screenWidth);

    }

    @HostListener('window:resize', ['$event'])
    onResize(event) {
        this.screenWidth = window.innerWidth;
        this.screenHeight = window.innerHeight - 156;
    }

}
