import { CorporationsListComponent } from './corporations-list/corporations-list.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CorporationsAddComponent } from './corporations-add/corporations-add.component';
import { CorporationsEditComponent } from './corporations-edit/corporations-edit.component';

const routes: Routes = [
    {
        path: '',
        component: CorporationsListComponent,

    },
    {
        path: 'add',
        component: CorporationsAddComponent,

    },
    {
        path: 'edit/:id',
        component: CorporationsEditComponent,

    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class CorporationsRoutingModule { }
